.explore-2 {
    .lists-container {
        display: flex;
        padding-left: 5px;
        margin-top: 3px;
        align-items: flex-start;

        .save-unpublished-btn:active {
            box-shadow: 0px 0px 2px 2px #7ee1ff;
        }

        .save-unpublished-btn {
            background-color: #42e5ffe3;
            color: black;
            padding: 5px;
            align-items: flex-start;
            cursor: pointer;

            .unpublished-count {
                color: #ffffff7a;
            }
        }
        
        .list {
            position: relative;
            opacity: 0.9;
            display: flex;
            flex-direction: column;
            width: 200px;
            height: 200px;
            background-size: cover;
            font-size: 10pt;
            margin-right: 5px;

            .delete-btn {
                background-color: white;
                color: black;
                bottom: 0px;
            }

            .edit-button, .delete-btn {
                margin: 0px;
                float: none;
                position: absolute;
                right: 0px;
                background-color: #ffffff66;
                padding: 3px;
                color: black;
                cursor: pointer;
            }

            .projects {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                
                .project.loaded {
                    box-shadow: 0px 0px 2px 2px white;
                    background-color: white;
                    color: black;
                }

                .project {
                    cursor: pointer;
                    background-color: #0000007a;
                    line-height: 14px;
                    margin: 3px;
                    align-items: flex-start;
                }
            }

            .list-title {
                text-align: center;
                margin-top: auto;
                margin-bottom: 5px;
                background-color: #0000007a;
            }
        }

        .new-list {
            display: flex;
            height: 250px;
            margin-right: 5px;
            background-color: #3513f036;
            width: 480px;
            padding-top: 3px;
            display: flex;

            .inner {
                position: relative;

                height: 100%;
                display: flex;
                margin-left: 5px;
                flex-direction: column;
                width: 50%;

                .delete-list-btn {
                    position: absolute;
                    right: 0px;
                    background-color: #ff3e1fc2;
                    bottom: 0px;
                    padding: 5px;
                    cursor: pointer;
                }

                .delete-list-btn:active {
                    box-shadow: 0px 0px 2px 2px red;
                }
            }

            .projects {
                max-height: 120px;
                overflow: scroll;

                .project {
                    display: flex;
                    .project-name {
                        padding-left: 5px;
                        flex: 1;
                        font-size: 9pt;
                    }

                    .delete-btn:active {
                        box-shadow: 0px 0px 2px 2px #6e9bff52;
                    }

                    .delete-btn {
                        cursor: pointer;
                        line-height: 12px;
                        margin-top: 2px;
                        background-color: #4a1dffc2;
                        border-radius: 10px;
                        color: black;
                        width: 15px;
                        height: 15px;
                        text-align: center;
                    }
                }
            }

            input {
                background-color: #23275c75;
                color: white;
                border: 1px solid #6e9bff52;
            } 

            .drop-here {
                flex: 1;
                display: flex;
                flex-direction: column;
                background-color: #01012575;
                
                span {
                    margin: auto;
                }
            }

            .btns {
                display: flex;
                
                .cancel-btn:active, .done-btn:active {
                    box-shadow: 0px 0px 2px 2px #6e9bff52;
                }

                .delete-btn.cancel-btn {
                    background-color: #ff0000c9;
                }

                .cancel-btn {
                    cursor: pointer;
                    text-align: center;
                    background-color: #0066ff45;
                    padding: 5px;
                    line-height: 18px;
                    height: 100%;
                    flex: 0 1;
                }

                .done-btn {
                    color: black;
                    cursor: pointer;
                    text-align: center;
                    flex: 1;
                    line-height: 30px;
                    background-color: #1d43ff;
                }
            }
        }
    }

    .image-handler {
        display: flex;
        flex-direction: column;
        height: 100%;

        img {
            width: 100%;
            height: 100%;
            opacity: 0.9;
        }
    }

    .work.list {
        opacity: 0.9;
        font-size: 9pt;

        background-size: cover;
        align-items: center;

        .list-author {
            margin-top: auto;
            text-align: center;
            font-size: 8pt;
            color: #ffffffd1;
            background-color: #00000059;
            min-width: 100px;
        }
        
        .list-title {
            text-align: center;
            background-color: #0000007a;
            min-width: 120px;
        }

        .projects {
            display: flex;
            flex-wrap: wrap;

            .project {
                margin-right: 10px;
                line-height: 13px;
                background-color: #0000007a;
                margin-bottom: 1px;
            }
          
        }
    }

}

