.explore-2 {
    .tags-editor {
        .tags-input {
            margin: 0px;
            background-color: #41A76F;
            width: 100%;
            border: none;
            font-size: 11pt;
            text-indent: 10px;
        }

        .tag-button {
            margin: 0px;
            padding: 5px;
            width: 100%;
            background-color: #9DFFD6;
            text-align: center;
            font-size: 8pt;
            color: black;
            padding: 2px;
            border: none;
        }

        .work-tags {
            display: flex;
            padding: 10px;
            text-align: center;
            flex-wrap: wrap;
            overlow-y: scroll;

            .work-tag {
                margin-right: 5px;
            }
        }
    }

    .annotations-svg {
        position: absolute;
        top: 230px;
        left: 0px;
        z-index: 3000;

        polygon {
            opacity: 0.6;
        }
    }

    .annotations-svg.ANNOTATION {
        polygon {
            fill: #285385;
        }
    }

    .annotations-view.ANNOTATION {
        background-color: #285385;
    } 

    .annotations-svg.TAGS {
        polygon {
            fill: #24794B;
        }
    }

    .annotations-view.TAGS {
        background-color: #24794B;
        height: 140px;
    } 

    .annotations-svg.EDITS {
        polygon {
            fill: #236074;
        }
    }

    .annotations-view.EDITS {
        background-color: #236074;
        height: 200px;
    } 
    
    .annotations-view {
        position: absolute;
        opacity: 0.9;
        top: 310px;
        width: 200px;
        height: 300px;
        z-index: 2000;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;

        .annotations-header {
            margin-left: 20px;
            color: #9DD0FF;
        }

        .annotation-text {
            background-color: #4576B1;
            color: #95CAF1;
            z-index: 4000;
            font-size: 9pt;
            border: none;
            border-radius: 0px;
            line-height: 15px;
            height: 60px;
            outline: none;
        }

        .submit-button {
            background-color: #9DD0FF;
            text-align: center;
            font-size: 8pt;
            padding: 2px;
            cursor: pointer;
            color: black;
        }
    }

    .new-edit-button {
        background-color: #42879e;
        text-align: center;
        padding: 3px;
        cursor: pointer;
    }
    
    .sequence-edits {
        padding: 5px;
        max-height: 170px;
        overflow-y: scroll;
        white-space: nowrap;

        .sequence-edit {
            display: flex;
            font-size: 8pt;
            background-color: #3b7a8e;
            margin: 2px;
            padding: 5px;
            
            .author {
                margin-left: 3px;
            }
            
            .date {
                margin-left: auto;
                overflow: hidden;
            }
        }
    }

    .annotations {
        max-height: 210px;
        overflow-y: scroll;
        .annotation {
            padding: 5px;
            font-size: 8pt;
            display: flex;
            background-color: #4576B1;
            margin: 5px;
            width: 95%;
            flex-wrap: wrap;

            .content {
                margin-left: 5px;
            }
        }
    }
}
